.multiply-container {
    min-height: 90px;
    padding: 0 15px;
}

.multiply {
    position: relative;
    height: 50px;
    padding: 6px 0;
    border: 1px solid teal;
}

.multiply .reorder-handle {
    top: 6px;
    left: 3px;
    font-size: 17px;
    color: #999;
}

.multiply input {
    width: 100%;
    height: 100%;
}

.multiply .form-group {
    margin-bottom: 0;
}

.multiply .row-buttons button {
    width: 40px;
    height: 40px;
    padding-top: 6px;
    padding-right: 0;
    padding-left: 0;
    border-radius: 20px;
    font-size: 14px;
}

.multiply-group {
    position: relative;
    height: 60px;
    border: 1px solid purple;
}

.multiply-group .action-icon {
    z-index: 2;
    height: 21px;
    width: 21px;
    background-color: transparent;
    font-size: 17px;
    color: #999;
    text-align: center;
    margin-top: 10px;

}

.multiply-group input {
    position: relative;
    z-index: 1;
    border: 0;
    width: 100%;
    background: transparent;
    outline: 0;
    margin-top: 3px;
    margin-left: -65px;
}

.hotbox {
    position: relative;
    display: block;
    width: 100%;
    height: 20px;
    border: 0;
    border-radius: 2px;
    overflow: hidden;
    text-align: center;
    transition: 200ms linear;
}

.hotbox.active {
    border: 1px dashed #aaa;
    height: 100px;
    transition: 300ms linear;
}

.ungrouped {
    min-height: 50px;
}

.locked {
    min-height: 70px;
}

.multiply-placeholder {
    width: 100%;
}
